.block-logos-grid {
  width: 100%;
  padding: 110px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f5f5f5;
}
.block-logos-grid__inner {
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}
@media (min-width: 768px) {
  .block-logos-grid__inner {
    padding: 0 15px;
  }
}
.block-logos-grid__inner .bg-img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  object-fit: contain;
  object-position: center center;
}
.block-logos-grid__wrapper {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1;
}
.block-logos-grid__wrapper .heading {
  width: 100%;
  margin-bottom: 60px;
  padding: 0 30px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 50px;
}
.block-logos-grid__wrapper .heading .block-title {
  color: rgb(var(--primary-color));
  text-align: center;
}
.block-logos-grid__wrapper .heading .block-title span,
.block-logos-grid__wrapper .heading .block-title strong {
  color: rgb(var(--secondary-color));
}
.block-logos-grid__wrapper .heading-content {
  text-align: center;
}
.block-logos-grid__wrapper .logos {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
}
@media (min-width: 768px) {
  .block-logos-grid__wrapper .logos {
    row-gap: 100px;
  }
}
.block-logos-grid__wrapper .logos .logo {
  padding: 20px 45px;
  position: relative;
  flex: 0 0 225px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid transparent;
  transition: all 0.3s linear;
}
.block-logos-grid__wrapper .logos .logo:hover {
  border: 1px solid rgb(var(--secondary-color));
}
.block-logos-grid__wrapper .logos .logo:hover img {
  filter: none;
}
.block-logos-grid__wrapper .logos .logo img {
  width: 100%;
  height: 45px;
  object-fit: contain;
  object-position: center center;
  filter: grayscale(1) brightness(0);
  background-color: transparent;
  transition: all 0.3s linear;
}
@media (min-width: 768px) {
  .block-logos-grid__wrapper .logos .logo img {
    height: 40px;
  }
}